body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  padding: 20px;
}

.new-product-name>div {
  width: -webkit-fill-available;
}

#hw1 .ui[class*="right labeled"].input>input.price-input {
  border-top-right-radius: .28571429rem !important;
  border-bottom-right-radius: .28571429rem !important;
  border-right-color: rgba(34,36,38,.15) !important;
}

#hw1 .price-column {
  min-width: 240px;
}